import React, { Component } from 'react';
import Particles from 'react-particles-js';
import './App.css';

import LandingPage from './LandingPage';
import ExperiencePage from './ExperiencePage';
import ContactPage from './ContactPage';
import AboutPage from './AboutPage';


const particlesOptions = {
  "particles": {
    "number": {
      "value": 80,
      "density": {
        "enable": true,
        "value_area": 800
      }
    },
    "color": {
      "value": "#ffffff"
    },
    "shape": {
      "type": "circle",
      "stroke": {
        "width": 0,
        "color": "#000000"
      },
      "polygon": {
        "nb_sides": 5
      },
      "image": {
        "src": "img/github.svg",
        "width": 100,
        "height": 100
      }
    },
    "opacity": {
      "value": 0.5,
      "random": false,
      "anim": {
        "enable": false,
        "speed": 1,
        "opacity_min": 0.1,
        "sync": false
      }
    },
    "size": {
      "value": 3,
      "random": true,
      "anim": {
        "enable": false,
        "speed": 40,
        "size_min": 0.1,
        "sync": false
      }
    },
    "line_linked": {
      "enable": true,
      "distance": 150,
      "color": "#ffffff",
      "opacity": 0.4,
      "width": 1
    },
    "move": {
      "enable": true,
      "speed": 6,
      "direction": "none",
      "random": false,
      "straight": false,
      "out_mode": "out",
      "bounce": false,
      "attract": {
        "enable": false,
        "rotateX": 600,
        "rotateY": 1200
      }
    }
  },
  "interactivity": {
    "detect_on": "canvas",
    "events": {
      "onhover": {
        "enable": true,
        "mode": "repulse"
      },
      "onclick": {
        "enable": true,
        "mode": "push"
      },
      "resize": true
    },
    "modes": {
      "grab": {
        "distance": 400,
        "line_linked": {
          "opacity": 1
        }
      },
      "bubble": {
        "distance": 400,
        "size": 40,
        "duration": 2,
        "opacity": 8,
        "speed": 3
      },
      "repulse": {
        "distance": 200,
        "duration": 0.4
      },
      "push": {
        "particles_nb": 4
      },
      "remove": {
        "particles_nb": 2
      }
    }
  },
  "retina_detect": true
};


class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      currentPage: 'Landing'
    }
  }

  renderPage = () => {
    switch(this.state.currentPage) {
        case 'Landing':
          return <LandingPage changePage={this.handlePageChange} />;
        case 'Experience':
          return <ExperiencePage changePage={this.handlePageChange} />;
        case 'About':
          return <AboutPage changePage={this.handlePageChange} />;
        case 'Contact':
          return <ContactPage changePage={this.handlePageChange} />;
        default:
          return <LandingPage />;
    }
  };

  handlePageChange = (page) => {
    this.setState({currentPage: page})
  };

  render() {
    return (
          <div className='App'>
              <Particles
                  className='particles'
                  style={{
                      width: '100%',
                      height: '100%',
                      background: `#000000`
                  }}
                  params={particlesOptions}
              />
              { this.renderPage() }
          </div>
      );
  }
}

export default App;
